@import "utils";

.how_to_apply {
  padding: 2.5rem;
  border-radius: 10px;
  margin: 30px 0 4rem;
  color: $white;

  @include media(mobile) {
    padding: 1.5rem;
    background-position: center;
  }

  h3 {
    @include h3;
    margin-bottom: 1.5rem;
  }

  .description {
    overflow: hidden;
  }

  a {
    text-decoration: underline;
    color: $white;
    font-weight: bold;
  }
}