@import "utils";

footer {
  height: 96px;
  background-color: $white;

  .wrapper {
    max-width: calc(730px + 1.5rem);
    padding: 0 1.5rem;

    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .jobInfo {
    @include media(mobile) {
      display: none;
    }

    h3 {
      @include h3;
      margin-bottom: .5rem;
    }

    p {
      color: $gray;
    }
  }

  .applyNow {
    @include media(mobile) {
      min-width: 100%;
    }

    .button {
      display: flex;
      justify-content: center;
      align-items: center;
      min-width: 140px;
      @include button1;

      @include media(mobile) {
        max-width: 100%;
      }
    }
  }

}

.darkMode footer {
  background-color: $very-dark-blue;

  .jobInfo {
    h3 {
      color: $white;
    }
  }
}