@import "breakpoints";

$violet : #5964E0;
$light-violet : #939BF4;
$very-dark-blue: #19202D;
$midnight : #121721;
$white: #FFFFFF;
$light-grey: #F4F6F8;
$gray: #9DAEC2;
$dark-gray: #6E8098;


@mixin button {
  font-size: 1rem;
  font-weight: bold;
  height: 3rem;
  max-width: 140px;
  width: 100%;
  border: none;
  border-radius: 5px;
}

@mixin button1 {
  @include button;
  background-color: $violet;
  color: $white;

  &:hover {
    background-color: $light-violet;
  }
}

@mixin button2 {
  @include button;
  background-color: $light-grey;
  color: $violet;

  &:hover {
    background-color: #c2bde2;
  }
}

@mixin h1 {
  font-size: 1.8rem;
  line-height: 2.2rem;

  @include media(mobile) {
    font-size: 1.3rem;
  }
}

@mixin h2 {
  font-size: 1.5rem;
  line-height: 2.1rem;
}

@mixin h3 {
  font-size: 1.25rem;
  font-weight: bold;
  line-height: 1.5rem;
}

@mixin h4 {
  font-size: .9rem;
  font-weight: bold;
  line-height: 1.2rem;
}

