@import "utils";

.jobDetails {
  background-color: $white;
  padding: 2.5rem;
  border-radius: 10px;

  @include media(mobile){
    padding: 1.5rem;
  }

  header {
    height: auto;
    display: grid;
    grid-template-columns: 3fr 1fr;
    align-items: center;
    margin-bottom: 2rem;

    @include media(mobile){
      grid-template-columns: 1fr;
      gap: 2rem;
    }
  }

  &__overview {
    height: inherit;
  }

  &__timeInfo {
    color: $gray;
    font-size: 1rem;

    span {
      font-size: 2rem;
    }
  }

  &__title {
    @include h1;
    color: $very-dark-blue;
    margin: 8px 0;
  }

  &__location {
    color: $violet;
    align-self: end;
  }

  &__applyNow .button {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    @include button1;

    @include media(mobile) {
      min-width: 100%;
    }
  }

  &__description {
    font-size: 1rem;
    line-height: 1.6rem;
    color:$gray;

    p {
      margin: 1.5rem 0;
    }

    strong {
      @include h3;
      color: $very-dark-blue;
    }

    ul {
      list-style: none;
      margin-left: 1.5em;

      li::before {
        content: "\2022";
        font-size: 1.5em;
        color: $violet;
        font-weight: bold;
        display: inline-block;
        width: 1em;
        margin-left: -1em;
      }
    }
  }
}

.darkMode .jobDetails{
  background-color: $very-dark-blue;

  &__title {
    color: $white;
  }

  &__description {
    strong {
      color: $white;
    }
  }
}