@import "utils";

@mixin searchBarInput {
  height: 100%;
  padding: 0 1.5rem;
  display: flex ;
  align-items: center;
  border-right: 1px solid rgba(0, 0, 0, .2);

  @include media(mobile) {
    border: none;
  }

  img {
    height: 24px;
    width: 24px;
  }

  input {
    height: 30%;
    width: 100%;
    margin-left: 1rem;
    border: none;
    font-size: 1rem;
    color: $very-dark-blue;
    background-color: transparent;

    &:focus {
      outline: none;
    }
  }
}

.searchBar {

  &__container {
    height: 80px;
    background-color: $white;
    border-radius: 10px;
    margin: -40px auto 0;
    padding-right: 1rem;
    display: grid;
    grid-template-columns: 3fr 2fr 1.3fr 1fr;
    align-items: center;

    @include media(tablet-s) {
      grid-template-columns: 1fr 1fr .5fr .3fr;
    }

    @include media(mobile) {
      grid-template-columns: 1fr 40px 66px;

      .filterButton {
        @include button;
        background-color: transparent;
      }

      .filter__modal {
        padding: 1.5rem;
        position: absolute;
        z-index: 1;
        background-color: rgba(0,0,0,.5);
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        align-items: center;
        justify-content: center;
        display: none;

        &.active {
          display: flex;
        }

        .closeModal {
          position: absolute;
          top: calc(50% - 140px);
          right: 1.5rem;
          background-color: transparent;
          border: none;
        }
      }

      .modal__container {
        background-color: $white;
        height: 217px;
        width: 100%;
        border-radius: 10px;
        display: grid;
        grid-template-rows: min-content min-content min-content;
        gap: 1.2rem;
        padding: 1.5rem;


        .searchBar__location {
          height: 60px;
          padding: 0;
          border-bottom: 1px solid rgba(0,0,0,.2);
        }

        .searchBar__fullTimeJob {
          padding: 0;
        }

        .searchBar__search--button {
          max-width: 100%;
        }
      }

    }
  }


  &__title {
    @include searchBarInput;

    input{
      @include media(mobile) {
        margin: 0;
      }
    }

    input{

    }
  }

  &__location {
    @include searchBarInput;
  }

  &__fullTimeJob {
    display: flex;
    align-items: center;
    padding-left: 1rem;

    .custom-checkbox {
      display: inline-flex;
      cursor: pointer;
      align-items: center;
    }

    .custom-checkbox-show--checkbox {
      background-color: $gray;
      border-radius: 2px;

      &:hover {
        background-color: $light-violet;
      }
    }

    .custom-checkbox > .custom-checkbox-input {
      display: none;
    }

    .custom-checkbox > .custom-checkbox-show {
      display: inline-block;
      width: 30px;
      height: 24px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: 60%;
    }

    .custom-checkbox > .custom-checkbox-text {
      line-height: 20px;
      width: 100%;
    }

    .custom-checkbox > .custom-checkbox-input:checked + .custom-checkbox-show--checkbox {
      background-color: $violet;
    }

    .custom-checkbox-text {
      font-size: 1rem;
      font-weight: bold;
      margin-left: 1rem;
      color: $very-dark-blue;
    }
  }

  &__search {
    @include media(mobile) {
      margin-left: 1rem;
    }
    &--button {
      @include button1;
    }
  }
}

.darkMode .searchBar {

  &__container {
    background-color: $very-dark-blue;

    @include media(mobile) {
      .modal__container {
        background-color: $very-dark-blue;
      }
    }

    input {
      color: $white;
    }
  }

  &__fullTimeJob {

    .custom-checkbox-text {
      color: $white;
    }
  }
}
