@import "utils";

.companyInfo {
  height: 140px;
  background-color: $white;
  border-radius: 10px;
  overflow: hidden;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr ;
  align-items: center;
  position: relative;
  top: -40px;

  @include media(mobile) {
    height: 200px;
    top: -40px;
    grid-template-columns: 1fr;
    gap: 10px ;
    grid-template-rows: min-content min-content;
    align-content: center;
    justify-items: center;
    overflow: initial;
  }

  &__logo {
    width: 100%;
    height: 100%;
    object-fit: cover;

    @include media(mobile) {
      background-color: $white;
      position: absolute;
      width: 50px;
      height: 50px;
      border-radius: 5px;
      top: -25px;
    }
  }

  &__content {
    padding: 2rem;

    @include media(mobile){
      padding: 1rem;
    }

    h2 {
      @include h2;
    }

    p {
      color: $gray;
      margin-top: .5rem;
    }
  }

  &__action {

    .button {
      display: flex;
      justify-content: center;
      align-items: center;
      @include button2;

      @include media(mobile) {
        margin: 0;
        width: 147px;
      }
    }
  }
}

.darkMode .companyInfo {
  background-color: $very-dark-blue;

  &__content {
    h2 {
      color: $white;
    }
  }

  &__action {
    .button {
      color: $white;
      background-color: #404040;

      @include media(mobile){
        background-color: rgba(40, 41, 88,.6);
        color: $violet;
      }

      &:hover {
        background-color: #5b5b5b;
      }

    }
  }
}